.usajobs-alert {
  animation: 0.8s fadeIn;
  border: 1px solid;
  border-left: 1rem solid;
  margin: 3rem 0;
  padding: 1.5rem 1.5rem 1rem 1rem;
  position: relative;
}
.flexbox .usajobs-alert {
  align-items: flex-start;
  display: flex;
}
.usajobs-alert--error {
  border-color: #cd2026;
}
.usajobs-alert--error,
.usajobs-alert--error .usajobs-alert__body,
.usajobs-alert--error:before {
  background-color: #f9dede;
}
.usajobs-modal {
  -webkit-overflow-scrolling: touch;
  bottom: 0;
  left: 0;
  min-height: 200px;
  opacity: 0;
  outline: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  transition: transform 0.3s linear, opacity 0.3s linear;
  visibility: hidden;
  width: auto;
  z-index: 9061;
}
@media screen and (min-width: 48em) {
